:root {
	--vff-bg-color: #3f3628;
	--vff-main-text-color: white;
	--vff-secondary-text-color: white;
	--vff-tertiary-text-color: #585144;
	--vff-main-accent-color: #e8ab55;
	--vff-secondary-accent-color: #e8ab55;
}
