.vff kbd,
.vff-header,
.vff-footer{
	font-family: 'mr-eaves-sans', Helvetica, Arial, sans-serif;
}
body {
	/*override if needed when using not-standalone option*/
	margin: 0;
	background-color: var(--vff-bg-color);
	/* background image gets set in javascript bc webpack was not playing along with image loading */
	/* background-image: url(./horse-butt.png); */
	background-repeat: no-repeat;
}
.vff{
	color: var(--vff-main-text-color);
}
.vff.vff-not-standalone{
	background-color: var(--vff-bg-color);
	color: var(--vff-main-text-color);
}
header.vff-header{
	/* header has no bg color so you can see the horse butt */
	/* background-color: var(--vff-bg-color); */
	background-color: transparent;
}
header.vff-header img.f-logo{
	height: auto;
	width: auto;
	max-width: unset;
	fill: rgba(0,0,0,0.9);
}
.vff input[type=text],
.vff input[type=number],
.vff input[type=tel],
.vff input[type=email],
.vff input[type=url],
.vff input[type=password],
.vff input[type=date],
.vff textarea,
.vff span.faux-form {
	border-bottom-color: var(--vff-secondary-text-color);
}
.vff textarea,
.vff input,
.vff select option {
	color: var(--vff-main-text-color);
}
.vff a,
.vff a:active {
	color: var(--vff-main-accent-color);
}/*placeholder*/
.vff ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: var(--vff-secondary-text-color);
	opacity: 1;
	font-weight: 300;
}
.vff ::-moz-placeholder { /* Firefox 19+ */
	color: var(--vff-secondary-text-color);
	opacity: 1;
	font-weight: 300;
}
.vff :-ms-input-placeholder { /* IE 10+ */
	color: var(--vff-secondary-text-color)!important;
	opacity: 1!important;
	font-weight: 300!important;
}
.vff :-moz-placeholder { /* Firefox 18- */
	color: #999;
	opacity: 1;
	font-weight: 300;
}/*faux input type date placeholder for iOS*/
.vff.vff-is-ios .field-datetype:not(.f-has-value) .f-answer > span::before{
	color: var(--vff-secondary-text-color);
	font-weight: 300;
	font-size: .72em;
}/*field-multiplechoicetype*/
.vff ul.f-radios li {
	border: 1px solid var(--vff-secondary-text-color);
}
.vff ul.f-radios li.f-selected,
.vff ul.f-radios li:active {
	border-color: var(--vff-main-accent-color);
	background-color: var(--vff-tertiary-text-color);
}
.vff ul.f-radios li.f-other.f-selected input {
	color: inherit;
}
.vff .f-key{
	color: var(--vff-main-accent-color);
